<template lang='pug'>
v-app
  v-toolbar(id='dark' dark flat)
    v-toolbar-title(id='title') {{title}}
    v-spacer
    v-toolbar-item
      v-btn(id='btn' flat app large to='/') Home
    v-toolbar-item
      v-btn(id='btn' flat app large to='/about') About
    v-toolbar-item
      v-btn(id='btn' flat app large to='/info') Further info
    v-toolbar-item
      v-btn(id='btn' flat app large to='/contact') Contact
    v-toolbar-item
      v-btn(id='sign' app large href='https://forms.ncl.ac.uk/view.php?id=2369806' exact) Sign up!
  v-content(id='grn')
    router-view
  v-footer(id='foot' height='auto' class='text-xs-center')
    v-flex(lg3 xs12)
      img(src='./assets/DC.png')
    v-flex(lg3 xs12)
      img(src='./assets/OL.png')
    v-flex(lg3 xs12)
      img(src='./assets/NCL.png')
    v-flex(lg3 xs12)
      img(src='./assets/EPSRC.png')
</template>

<script>
export default {
  data() {
    return {
      title: "Open Lab Music Summer School"
    };
  },
  name: "App"
};
</script>

<style lang="sass">
$drkgreen: #0E3A3F;
$green: #1B484C;
$york: #8BE7BC;

#title
  font-size: 25pt;
  font-family: Acme;
#dark
  background: $green;
#grn
  background: $drkgreen;
#btn
  background: $green;
  color: white;
  text-transform: capitalize;
  font-family: Acme;
  font-size: 22pt;
#sign
  background: $york;
  text-transform: none;
  color: black;
  font-size: 22pt;
  font-family: Acme;
#foot
  background: $green;
  text-align: center;
  img
    height: 100px;
    padding: 20px 0px 20px 0px;
</style>



