<template lang="pug">
div(id='content') 
    v-container
        h1
            | Further Info
        h2
            | What will it cost?
        p
            | Nothing.
            | It is a free experience for young people, provided as part of our work in Digital 
            | Civics (looking at ways in which working with professional musicians can benefit young 
            | people’s musical skills).
        h2
            | Where is it?
        p
            | The summer school will be held at Open Lab, Newcastle University from Monday – Thursday.  
            | The final gig will be held at the Sage Gateshead on Friday 3rd August.
        h2 
            | Aims
        ul
            li
                | gain experience of organising and running a live gig for professional musicians
            li 
                | improve sound engineering skills
            li
                | learn how to set up equipment for a live gig
            li
                | learn how to live mix a gig
        br
        h2
            | Who is running the summer school?
        p
            | It will be run by <strong id='grn'>Rebecca Nicholson</strong> – a qualified secondary Music teacher and postgraduate researcher.
            | There will be others who help during the day such as people with expertise in sound engineering and event management.

</template>


<script>
export default {
data () { 
    return { 
    }
}
}
</script>

