<template lang="pug">
div(id='content') 
    v-container
        h1
            | About
        v-layout(row wrap)
            v-flex(lg6 xs12)
                h2
                    | What is the project about?
                p
                    | We would like to invite you to participate in a Newcastle University project 
                    | to explore how taking part in planning an event can improve musical knowledge 
                    | as well as 21st Century Skills (these are communication, cooperation, creativity 
                    | and critical thinking).
                h2
                    | Who are Open Lab?
                p
                    | We are an interaction design and ubiquitous computing research group based at 
                    | Newcastle University. Our work focuses on the experience-centred design of digital 
                    | technology and applied challenges in ubiquitous computing. Many members of the group 
                    | originate from a disciplines outside computer science, including psychology, electronic 
                    | engineering, sociology, clinical sciences, education, design and fine art.
                
            v-flex(xs12 lg6)        
                h2 
                    | What happens afterwards?
                p
                    | The information collected from tasks you complete will be used to inform research 
                    | to find out if planning a music event improves your 21st Century Skills as well as 
                    | your knowledge of music.  The results of the analysis of your submitted work will be 
                    | shared in a paper that other academics will read as well as with an external examiner 
                    | who will read the final submitted project.
                h2
                    | What am I being asked to do?

                p
                    | The project will involve you being part of a team that plans an event for a 
                    | local professional musician.  The project will require you to work with other 
                    | people in your team to complete group tasks as well as completing individual tasks.  
                    | These tasks will be things like sending emails, writing meeting minutes or learning 
                    | to mix live music for example.
                
        
</template>


